import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { RxOverlayService } from './overlay.service';

@Injectable()
export class RxOverlayRequestsInterceptor implements HttpInterceptor {
  constructor(private rxOverlayService: RxOverlayService) {}

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const currentOverlayContext = this.rxOverlayService.getCurrentOverlayContext();

    if (currentOverlayContext?.overlayGroupId && !request.headers.get('request-overlay-group')) {
      request = request.clone({
        headers: request.headers.set('request-overlay-group', currentOverlayContext.overlayGroupId)
      });
    }

    return next.handle(request);
  }
}
