import { clone, get, has, last } from 'lodash';
import { startWith } from 'rxjs/operators';

import { Constructor } from '@helix/platform/utils';

import { IPlainObject } from '../common-types/plain-object.interface';
import { IDesignerElementService } from '../designer/designer.types';
import { IServerActionChangedProperty } from './server-action-changed-property.interface';

export function RxServerActionMixin<TBase extends Constructor>(Base: TBase) {
  return class RxServerAction extends Base {
    // TODO-VS: add types after rappid update
    initialize(config: any): any {
      // @ts-ignore
      super.initialize(config);

      this.setDataDictionaryBranch();

      // TODO-VS: add types after rappid update
      // @ts-ignore
      this.on(
        'change:elementModel',
        (element: any, elementModel: any, changedProperty: IServerActionChangedProperty) => {
          if (startWith(changedProperty.propertyPath, 'elementModel/inputMap')) {
            const inputMapPropertyPath: string = last(changedProperty.propertyPath.split('/'));

            let inputMapPropertyValue = changedProperty.propertyValue;
            const isCommandManagerOperation = has(changedProperty, 'commandManager');

            if (isCommandManagerOperation) {
              inputMapPropertyValue = elementModel.inputMap[inputMapPropertyPath];
            }

            this.setDataDictionaryBranch();

            this.onInputMapChanged(
              element,
              elementModel.inputMap,
              inputMapPropertyPath,
              inputMapPropertyValue,
              isCommandManagerOperation
            );
          }
        }
      );
    }

    clearOutputMap(): void {
      // @ts-ignore
      this.prop('elementModel/outputMap', [], {
        rxSilent: true,
        rewrite: true
      });
    }

    getElementService(type: string): IDesignerElementService {
      return null;
    }

    getInputMap(): IPlainObject {
      // @ts-ignore
      return clone(this.prop('elementModel/inputMap')) || {};
    }

    // TODO-VS: add types after rappid update
    onInputMapChanged(
      element: any,
      inputMap: IPlainObject,
      inputMapPropertyPath: string,
      inputMapPropertyValue: string,
      isCommandManagerOperation: boolean
    ): void {}

    setDataDictionaryBranch(): void {
      // @ts-ignore
      const elementService = this.getElementService(this.prop('type'));

      elementService.setCommonDataDictionaryBranch(
        // @ts-ignore
        this.prop('elementModel/guid'),
        // @ts-ignore
        elementService.buildDataDictionaryBranch(this.prop('elementModel'))
      );
    }

    setInputMap(inputMap: IPlainObject, options?: any): void {
      // use 'rewrite' option to prevent the default 'merge' behavior
      // @ts-ignore
      this.prop('elementModel/inputMap', inputMap, {
        rxSilent: true,
        rewrite: true,
        silent: get(options, 'isSilent', false)
      });
    }
  };
}
