import { Injectable } from '@angular/core';
import { RxUtilityModalsService } from '@helix/platform/ui-kit';
import { from, Observable, throwError } from 'rxjs';
import { catchError, switchMap } from 'rxjs/operators';
import { RX_ERROR_HANDLING } from '../error-handling/error-handling.constant';
import { IHttpOptions } from '../http';

@Injectable({
  providedIn: 'root'
})
export class RxDefinitionUpdateService {
  constructor(private rxUtilityModalsService: RxUtilityModalsService) {}

  execute(updateFn: (httpOptions?: IHttpOptions) => Observable<any>): Observable<any> {
    return updateFn().pipe(
      catchError((error) => {
        if (error.status === RX_ERROR_HANDLING.optimisticLockErrorHttpStatus) {
          return from(this.rxUtilityModalsService.confirmExternalChange(error.error[0].messageText)).pipe(
            switchMap((isConfirmed) => {
              if (isConfirmed) {
                return updateFn({
                  headers: {
                    'Override-Optimistic-Lock': 'true'
                  }
                });
              } else {
                return throwError(error);
              }
            })
          );
        } else {
          return throwError(error);
        }
      })
    );
  }
}
