import { Injectable } from '@angular/core';
import { chain, map } from 'lodash';

import { IFunctionDescriptor } from '../function/function-descriptor.interfaces';
import {
  IDataDictionary,
  IDataDictionaryBranch,
  IFunctionDataDictionaryDescriptor,
  NodeInfoType
} from './data-dictionary.types';

@Injectable({
  providedIn: 'root'
})
export class RxDataDictionaryUtils {
  addTooltips(
    dataDictionary: IDataDictionary,
    parentNodeLabel?: string
  ): Array<IDataDictionaryBranch & { tooltip: string }> {
    return map(dataDictionary, (node) => {
      const tooltip = parentNodeLabel ? `${parentNodeLabel} > ${node.label}` : node.label;

      return {
        ...node,
        tooltip,
        children: node.children ? this.addTooltips(node.children, tooltip) : null
      };
    });
  }

  // TODO-VS: refactor to use below method
  getFunctionsDataDictionaryBranch(functionDescriptors: IFunctionDataDictionaryDescriptor[]): IDataDictionaryBranch[] {
    return chain(functionDescriptors)
      .groupBy('category')
      .map((functionDescriptors: IFunctionDataDictionaryDescriptor[], category: string) => ({
        label: category,
        children: map(functionDescriptors, (functionDescriptor) => ({
          label: functionDescriptor.name + '()',
          icon: 'd-icon-mathematical_function',
          expression: functionDescriptor.name + '()',
          info: {
            type: NodeInfoType.function,
            data: functionDescriptor
          }
        }))
      }))
      .value();
  }

  getFunctionDataDictionaryBranch(functionDescriptors: IFunctionDescriptor[]): IDataDictionaryBranch[] {
    return chain(functionDescriptors)
      .groupBy('type')
      .map((functionDescriptors: IFunctionDescriptor[], functionType: string) => ({
        label: functionType,
        children: map(functionDescriptors, (functionDescriptor) => ({
          label: functionDescriptor.name + '()',
          icon: 'd-icon-mathematical_function',
          expression: functionDescriptor.name + '()',
          info: {
            type: NodeInfoType.function,
            data: functionDescriptor
          }
        }))
      }))
      .value();
  }
}
