import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { isUndefined, isString } from 'lodash';
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { RX_ADMINISTRATION } from './administration.constant';
import { ISystemConfiguration, SystemConfigurationValue, SystemConfigurationValuesById } from './administration.types';

@Injectable({
  providedIn: 'root'
})
export class RxSystemConfigurationService {
  constructor(private httpClient: HttpClient) {}

  private systemConfigurations: SystemConfigurationValuesById = {};

  initialize(): Observable<ISystemConfiguration[]> {
    return this.queryConfiguration(['Submitter-Mode']).pipe(
      tap((systemConfigurations: ISystemConfiguration[]) => {
        this.systemConfigurations = systemConfigurations.reduce<SystemConfigurationValuesById>(
          (configs, systemConfiguration) => {
            configs[systemConfiguration.id] = systemConfiguration.value;

            return configs;
          },
          {}
        );
      })
    );
  }

  getConfigurationSync(identifier: string): SystemConfigurationValue {
    if (this.systemConfigurations) {
      const configValue = this.systemConfigurations[identifier];

      if (isUndefined(configValue)) {
        throw new Error(`System configuration ${identifier} is not available.`);
      }

      return configValue;
    } else {
      throw new Error('System configuration service is not initialized.');
    }
  }

  getConfiguration(configurationName: string): Observable<ISystemConfiguration> {
    return this.httpClient.get<ISystemConfiguration>(
      `${RX_ADMINISTRATION.systemConfigurationUrl}/${configurationName}`
    );
  }

  setConfiguration(configurationName: string, configurationValue: any): Observable<any> {
    return this.httpClient.put(`${RX_ADMINISTRATION.systemConfigurationUrl}/${configurationName}`, {
      name: configurationName,
      value: isString(configurationValue) ? configurationValue : JSON.stringify(configurationValue)
    });
  }

  queryConfiguration(identifiers: string[]): Observable<ISystemConfiguration[]> {
    const queryString = identifiers.map((identifier) => `identifier=${encodeURIComponent(identifier)}`).join('&');

    return this.httpClient.get<ISystemConfiguration[]>(
      `${RX_ADMINISTRATION.systemConfigurationUrl}/query?${queryString}`
    );
  }
}
