import { Injectable } from '@angular/core';
import { RxLogService } from '../logging/log.service';
import { RxGlobalCacheService } from './global-cache.service';
import { RX_BUNDLE } from '../bundle/bundle.constant';
import { map, shareReplay, switchMap } from 'rxjs/operators';
import { Observable, ReplaySubject } from 'rxjs';
import { IBundleDescriptor } from '../bundle';
import { IScopeSelectionOption } from '../common-types';
import { RxActionTypeDataPageService } from '../action-type/action-type-data-page.service';
import { IActionType } from '../action-type/action-type.interfaces';
import { IDataPageResult } from '../data-page';
import { RxDefinitionService } from '../definition/definition.service';

@Injectable({
  providedIn: 'root'
})
export class RxBundleCacheService {
  private actionTypes$: Observable<IActionType[]> = this.rxActionTypeDataPageService
    .get({ params: { requireDependent: true } })
    .pipe(
      map((actionTypes: IDataPageResult) => actionTypes.data as IActionType[]),
      shareReplay(1)
    );

  private bundleIdValue: string;
  private bundleIdSubject$ = new ReplaySubject<string>(1);
  public bundleId$ = this.bundleIdSubject$.asObservable();

  constructor(
    private rxActionTypeDataPageService: RxActionTypeDataPageService,
    private rxLogService: RxLogService,
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxDefinitionService: RxDefinitionService
  ) {}

  get bundleId(): string {
    return this.bundleIdValue;
  }

  set bundleId(value: string) {
    this.bundleIdValue = value;

    this.bundleIdSubject$.next(value);

    this.rxLogService.debug(`RxBundleCacheService: bundle ID set to ${value}`);
  }

  getActionTypes(): Observable<IActionType[]> {
    return this.actionTypes$;
  }

  getCurrentBundleDescriptor(): Observable<IBundleDescriptor> {
    return this.bundleId$.pipe(switchMap((bundleId) => this.rxGlobalCacheService.getBundleDescriptor(bundleId)));
  }

  getDefinitionScopeName(definitionScopeType: string): Observable<string> {
    return this.getCurrentBundleDescriptor().pipe(
      map((bundleDescriptor) => this.rxDefinitionService.getScopeName(definitionScopeType, bundleDescriptor))
    );
  }

  getDefinitionScopeSelectionOptions(): Observable<IScopeSelectionOption[]> {
    return this.getCurrentBundleDescriptor().pipe(
      map((bundleDescriptor) =>
        bundleDescriptor.isApplication
          ? RX_BUNDLE.applicationDefinitionScopeSelectionOptions
          : RX_BUNDLE.libraryDefinitionScopeSelectionOptions
      )
    );
  }
}
