import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';
import { Observable } from 'rxjs';
import { RxSessionExpirationService } from '../security/session-expiration/session-expiration.service';

@Injectable({
  providedIn: 'root'
})
export class RxKeepSessionAliveResolver implements Resolve<any> {
  constructor(private rxSessionExpirationService: RxSessionExpirationService) {}

  resolve(): Observable<any> {
    return this.rxSessionExpirationService.keepSessionAlive();
  }
}
