import { ActivatedRouteSnapshot, DetachedRouteHandle, RouteReuseStrategy } from '@angular/router';
import { RX_APPLICATION } from './application.constant';
import { isEqual } from 'lodash';

// Customize shouldReuseRoute, use default implementation for other methods.
export class ShellRouteReuseStrategy implements RouteReuseStrategy {
  shouldReuseRoute(current: ActivatedRouteSnapshot, next: ActivatedRouteSnapshot): boolean {
    if (
      next.data?.routeReuseStrategy === RX_APPLICATION.routeReuseStrategies.checkParentParams &&
      current.parent &&
      current.parent.routeConfig === next.parent?.routeConfig
    ) {
      return isEqual(next.parent.params, current.parent.params);
    } else {
      return (
        current.routeConfig === next.routeConfig ||
        (current.routeConfig?.data?.routerGroup &&
          current.routeConfig.data.routerGroup === next.routeConfig?.data?.routerGroup)
      );
    }
  }

  shouldAttach(route: ActivatedRouteSnapshot): boolean {
    return false;
  }

  shouldDetach(route: ActivatedRouteSnapshot): boolean {
    return false;
  }

  retrieve(route: ActivatedRouteSnapshot): DetachedRouteHandle | null {
    return null;
  }

  store(route: ActivatedRouteSnapshot, handle: DetachedRouteHandle | null): void {}
}
