import { HttpInterceptor, HttpRequest, HttpEvent, HttpHandler, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { RxHttpParameterCodec } from './http-parameter-codec.class';

@Injectable({
  providedIn: 'root'
})
export class RxHttpParamsInterceptor implements HttpInterceptor {
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const params = new HttpParams({
      encoder: new RxHttpParameterCodec(),
      fromString: request.params.toString()
    });

    return next.handle(request.clone({ params }));
  }
}
