import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { RxUrlUtilsService } from '@helix/platform/utils';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { RxFeatureService } from '../services/feature/feature.service';
import { IUser } from './user.interface';

@Injectable({
  providedIn: 'root'
})
export class RxUserService {
  constructor(
    private httpClient: HttpClient,
    private rxFeatureService: RxFeatureService,
    private router: Router,
    private rxUrlUtilsService: RxUrlUtilsService
  ) {}

  getUser(id: string, bundleId?: string): Observable<IUser> {
    return this.httpClient
      .get<IUser>(`/api/rx/application/user/${id}`, {
        headers: new HttpHeaders({
          'default-bundle-scope': bundleId ?? ''
        }),
        observe: 'response'
      })
      .pipe(
        map((response) => {
          this.rxFeatureService.enableFeatures((response.headers.get('Enabled-Features') || '').split(','));

          return {
            ...response.body,
            modifiedDate: new Date(response.body.modifiedDate),
            ssoProviderType: response.headers.get('sso-provider-type')
          };
        })
      );
  }

  getCurrentUser(): Observable<IUser> {
    return this.getUser('$USER$', this.rxUrlUtilsService.getBundleIdFromUrl());
  }
}
