import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { last } from 'lodash';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { IPlainObject } from '../common-types';
import {
  IAdminComponentDefinition,
  IAdminComponentGridData,
  IAdminComponentSetting,
  IAdminComponentSettings,
  IAdminNavigationMenuItem,
  IAdminSetting
} from './administration.types';
import { RxCommandFactoryService } from '../command';

const API_URL = '/api/rx/application/admin-settings/local';
const SETTINGS_API_URL = '/api/rx/application/admin-settings/component';

@Injectable({
  providedIn: 'root'
})
export class RxAdminSettingsService {
  constructor(private httpClient: HttpClient, private rxCommandFactoryService: RxCommandFactoryService) {}

  getComponentGridData(componentName: string, customHeaders?: IPlainObject): Observable<IAdminComponentGridData> {
    return this.httpClient.get<IAdminComponentGridData>(`${API_URL}/component-griddata/${componentName}`, {
      headers: new HttpHeaders(customHeaders || {})
    });
  }

  getComponentDefinition(componentName: string, customHeaders?: IPlainObject): Observable<IAdminComponentDefinition> {
    return this.httpClient.get<IAdminComponentDefinition>(`${API_URL}/component-definition/${componentName}`, {
      headers: new HttpHeaders(customHeaders || {})
    });
  }

  getAdminNavigationMenuItems(): Observable<IAdminNavigationMenuItem[]> {
    return this.httpClient
      .get<{ AdminNavigationMenu: { items: IAdminNavigationMenuItem[] } }>(`${API_URL}/navigation-menu`)
      .pipe(
        map(
          (navigationMenu: { AdminNavigationMenu: { items: IAdminNavigationMenuItem[] } }) =>
            navigationMenu.AdminNavigationMenu.items
        )
      );
  }

  getComponentSettings(componentName: string, customHeaders?: IPlainObject): Observable<IAdminComponentSettings> {
    return this.httpClient.get<IAdminComponentSettings>(`${API_URL}/component-settings/${componentName}`, {
      headers: new HttpHeaders(customHeaders || {})
    });
  }

  deleteComponentSettings(componentName: string, customHeaders?: IPlainObject): Observable<any> {
    return this.httpClient.delete(`${API_URL}/component-settings/${componentName}`, {
      headers: new HttpHeaders(customHeaders || {})
    });
  }

  createComponentSettings(
    componentName: string,
    data: IAdminComponentSetting[],
    customHeaders?: IPlainObject
  ): Observable<string> {
    return this.httpClient
      .post(`${API_URL}/component-settings/${componentName}`, data, {
        headers: new HttpHeaders(customHeaders || {}),
        observe: 'response'
      })
      .pipe(map((response) => last(response.headers.get('location')?.split('/')) || ''));
  }

  updateComponentSettings(componentName: string, data: IAdminComponentSetting[], customHeaders?: IPlainObject) {
    return this.httpClient.put(`${API_URL}/component-settings/${componentName}`, data, {
      headers: new HttpHeaders(customHeaders || {})
    });
  }

  getAdminSetting(componentName: string, customHeaders?: IPlainObject): Observable<IAdminSetting> {
    return this.httpClient.get<IAdminSetting>(`${SETTINGS_API_URL}/${componentName}`, {
      headers: new HttpHeaders(customHeaders || {})
    });
  }

  createAdminSetting(data: IAdminSetting) {
    return this.httpClient.post(SETTINGS_API_URL, data);
  }

  updateAdminSetting(data: IAdminSetting) {
    return this.httpClient.put(`${SETTINGS_API_URL}/${data.componentName}`, data);
  }

  deleteAdminSetting(definitionNames: string[]): Observable<any> {
    return this.rxCommandFactoryService
      .forResourceType('com.bmc.arsys.rx.application.admin.command.DeleteAdminSettingsComponentDefinitionsCommand')
      .execute({ definitionNames });
  }
}
