import { Injectable } from '@angular/core';
import { RxBundleDataPageService } from '../bundle/bundle-data-page.service';
import { IBundleDescriptor } from '../bundle/bundle.interfaces';
import { defaults } from 'lodash';
import { Observable, ReplaySubject } from 'rxjs';
import { map, shareReplay, switchMap, tap } from 'rxjs/operators';
import { RxLogService } from '../logging/log.service';
import { IDataPageResult } from '../data-page';
import { RxBundleLoadType } from '../bundle/bundle-load.types';
import { RX_APPLICATION } from '../application';
import { IFunctionDescriptor } from '../function/function-descriptor.interfaces';
import { RxFunctionDataPageService } from '../function/function-data-page.service';

@Injectable({
  providedIn: 'root'
})
export class RxGlobalCacheService {
  private bundleDescriptorsById: { [id: string]: Observable<IBundleDescriptor> } = {};
  private bundleDescriptors: Observable<IBundleDescriptor[]> | null = null;
  private functionDescriptors: Observable<IFunctionDescriptor[]> = null;

  private applicationIdValue = '';
  private applicationIdSubject = new ReplaySubject<string>(1);
  public applicationId$ = this.applicationIdSubject.asObservable();

  get applicationId(): string {
    return this.applicationIdValue;
  }

  set applicationId(value: string) {
    if (this.applicationIdValue !== value) {
      this.applicationIdValue = value;

      this.applicationIdSubject.next(value);

      this.rxLogService.debug(`RxGlobalCacheService: application ID set to ${value}`);
    }
  }

  constructor(
    private rxBundleDataPageService: RxBundleDataPageService,
    private rxFunctionDataPageService: RxFunctionDataPageService,
    private rxLogService: RxLogService
  ) {}

  getBundleDescriptors(): Observable<IBundleDescriptor[]> {
    if (!this.bundleDescriptors) {
      this.bundleDescriptors = this.rxBundleDataPageService.get().pipe(
        map(
          (bundleDescriptors: IDataPageResult) =>
            bundleDescriptors.data.map((dataPageData) => {
              const defaultUiOptions = {
                options: {
                  loadJs: RxBundleLoadType.always,
                  loadCss: RxBundleLoadType.always
                }
              };

              let uiOptions;

              try {
                uiOptions = JSON.parse(dataPageData.uiOptions) ?? defaultUiOptions;

                if (Boolean(uiOptions.options) && typeof uiOptions.options === 'object') {
                  defaults(uiOptions.options, defaultUiOptions.options);
                } else {
                  this.rxLogService.warning(`Invalid structure of uiOptions in bundle ${dataPageData.name}.`);

                  uiOptions = defaultUiOptions;
                }
              } catch (e) {
                this.rxLogService.warning(`Cannot parse uiOptions in bundle ${dataPageData.name}.`);

                uiOptions = defaultUiOptions;
              }

              return {
                ...dataPageData,
                uiOptions
              };
            }) as IBundleDescriptor[]
        ),
        shareReplay(1)
      );
    }

    return this.bundleDescriptors;
  }

  getFunctionDescriptors(): Observable<IFunctionDescriptor[]> {
    if (!this.functionDescriptors) {
      this.functionDescriptors = this.rxFunctionDataPageService.get().pipe(
        map((dataPage) => dataPage.data as IFunctionDescriptor[]),
        shareReplay(1)
      );
    }

    return this.functionDescriptors;
  }

  getLicensedBundleDescriptors(): Observable<IBundleDescriptor[]> {
    return this.getBundleDescriptors().pipe(
      map((bundleDescriptors) => bundleDescriptors.filter((bundleDescriptor) => bundleDescriptor.isLicensed))
    );
  }

  getBundleDescriptor(bundleId: string): Observable<IBundleDescriptor> {
    if (!this.bundleDescriptorsById[bundleId]) {
      this.bundleDescriptorsById[bundleId] = this.getBundleDescriptors().pipe(
        map(
          (bundleDescriptors) =>
            bundleDescriptors.find((bundleDescriptor) => bundleDescriptor.id === bundleId) as IBundleDescriptor
        ),
        shareReplay(1)
      );
    }

    return this.bundleDescriptorsById[bundleId];
  }

  getApplicationBundleDescriptor(): Observable<IBundleDescriptor> {
    return this.applicationIdSubject.pipe(switchMap((applicationId) => this.getBundleDescriptor(applicationId)));
  }

  private _getBundleFriendlyName(descriptor: IBundleDescriptor, defaultValue?: string): string {
    return descriptor?.friendlyName || descriptor?.id || defaultValue || '';
  }

  getBundleFriendlyName(bundleId: string, defaultValue?: string): Observable<string> {
    return this.getBundleDescriptor(bundleId).pipe(
      map((bundleDescriptor) => this._getBundleFriendlyName(bundleDescriptor, defaultValue))
    );
  }

  getBundleDisplayName(bundleId: string): Observable<string> {
    return this.getBundleDescriptor(bundleId).pipe(
      map(
        (bundleDescriptor) =>
          (this.applicationId !== RX_APPLICATION.innovationStudioBundleId && bundleDescriptor?.localizedDisplayName) ||
          this._getBundleFriendlyName(bundleDescriptor)
      )
    );
  }

  clear() {
    this.bundleDescriptorsById = {};
    this.bundleDescriptors = null;
    this.functionDescriptors = null;

    this.rxLogService.debug('RxGlobalCacheService: cleared.');
  }
}
