import { Injector } from '@angular/core';
import { Observable, of } from 'rxjs';

import {
  ExpressionOperatorGroup,
  ExpressionOperatorRowsByGroup
} from '../expression-configurator/expression-configurator-operator.types';
import { IExpressionConfiguratorDefaultPropertyConfig } from '../expression-configurator/expression-configurator.interfaces';
import { RxExpressionConfigurator } from '../expression-configurator/expression-configurator.class';
import { IDataDictionary } from '../data-dictionary/data-dictionary.types';

export abstract class RxServerActionExpressionConfigurator extends RxExpressionConfigurator {
  commonDataDictionary$: Observable<IDataDictionary>;

  private dataDictionaryService = this.getDataDictionaryService();

  protected constructor(protected injector: Injector) {
    super();

    this.commonDataDictionary$ = this.dataDictionaryService.commonDataDictionary$;

    this.configureForProperty({
      propertyPath: /inputMap\/.*/,
      dataDictionary$: this.getInputMapDataDictionary(),
      validateExpression: (propertyPath, expression) => this.validateInputMapExpression(propertyPath, expression),
      operators: this.getOperatorRowsByGroup(ExpressionOperatorGroup.Math)
    });
  }

  protected abstract getDataDictionaryService(): any;

  getDefaultConfig(): IExpressionConfiguratorDefaultPropertyConfig {
    return {
      ...super.getDefaultConfig(),
      dataDictionary$: this.commonDataDictionary$,
      operators: ExpressionOperatorRowsByGroup.get(ExpressionOperatorGroup.AllClient)
    };
  }

  getInputMapDataDictionary(): Observable<IDataDictionary> {
    return this.commonDataDictionary$;
  }

  validateInputMapExpression(propertyPath: string, expression: string): Observable<boolean> {
    return of(true);
  }
}
