import { DOCUMENT } from '@angular/common';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';

import { AdaptTranslateService } from '@bmc-ux/adapt-angular';
import { TranslateService } from '@ngx-translate/core';
import { findKey, some } from 'lodash';
import moment from 'moment-es6';
import { Observable, of } from 'rxjs';
import { filter, map, mapTo, shareReplay, switchMap, take, tap } from 'rxjs/operators';
import { RxCurrentUserService } from '../user/current-user.service';
import {
  ILocaleInfo,
  ILocalizedStrings,
  ILocalizedStringsForLoginPageResponse,
  RX_DEFAULT_STRINGS
} from './localization.types';
import { RxRssoDebugService } from '../dev/rsso-debug.service';

const SUPPORTED_LOCALES: { [key: string]: ILocaleInfo } = {
  de: { adapt: 'de-de' },
  en: { adapt: 'en-us' },
  es: { adapt: 'es-es' },
  fr: { adapt: 'fr-fr' },
  it: { adapt: 'it-it' },
  ja: { adapt: 'ja-jp' },
  ko: { adapt: 'ko-kr' },
  nl: { adapt: 'nl-nl' },
  pt: { adapt: 'pt-br' },
  ru: { adapt: 'ru-ru' },
  sv: { adapt: 'sv-se' },
  da: { adapt: 'da-dk' },
  no: { moment: 'nb', angular: 'nb', adapt: 'no-no', similar: ['nb', 'nn'] },
  'zh-Hans': { moment: 'zh-CN', adapt: 'zh-cn', similar: ['zh-CN'] }
};

const LOGIN_LOCALE = 'login';
const DEFAULT_LOCALE = 'en';

@Injectable({
  providedIn: 'root'
})
export class RxLocalizationService {
  currentLocale: string = DEFAULT_LOCALE;
  angularLocale: string = DEFAULT_LOCALE;

  onTranslationsLoaded$ = this.translateService.onLangChange.pipe(shareReplay(1));

  loginLocalizedStrings$ = this.getLocalizedStringsForLoginPage().pipe(shareReplay(1));

  private preferredLocale$: Observable<string> = this.rxCurrentUserService.user$.pipe(
    filter(Boolean),
    take(1),
    map(() => this.rxCurrentUserService.getPreferredLocale() || this.rxCurrentUserService.getPreferredUserLocale()),
    switchMap((preferredLocale) =>
      preferredLocale
        ? of(preferredLocale)
        : this.loginLocalizedStrings$.pipe(map((response) => Object.keys(response.body)[0]))
    ),
    map((locale) => this.normalizeLocale(locale)),
    shareReplay(1)
  );

  constructor(
    private httpClient: HttpClient,
    private translateService: TranslateService,
    private rxCurrentUserService: RxCurrentUserService,
    private adaptTranslateService: AdaptTranslateService,
    private rxRssoDebugService: RxRssoDebugService,
    @Inject(DOCUMENT) private document: any,
    @Inject(RX_DEFAULT_STRINGS) private defaultStrings: ILocalizedStrings
  ) {}

  getDefaultApplicationStrings(): ILocalizedStrings {
    return this.defaultStrings;
  }

  setDefaultApplicationStrings(defaultApplicationStrings: ILocalizedStrings) {
    this.defaultStrings = { ...this.defaultStrings, ...defaultApplicationStrings };
  }

  initLoginTranslations(): Observable<ILocalizedStringsForLoginPageResponse> {
    return this.loginLocalizedStrings$.pipe(
      map((response) => response.body),
      tap((payload) => {
        const localeKey = Object.keys(payload)[0];

        this.setLocale(this.normalizeLocale(localeKey));
        this.translateService.setTranslation(LOGIN_LOCALE, payload[localeKey], true);
      }),
      switchMap(() => this.translateService.use(LOGIN_LOCALE))
    );
  }

  initTranslations(useDefault = false): Observable<string> {
    return (useDefault ? of(DEFAULT_LOCALE) : this.preferredLocale$).pipe(
      switchMap((locale) => this.translateService.use(locale).pipe(mapTo(locale))),
      tap((locale) => {
        this.setLocale(locale);

        const adaptLocale = this.getCurrentLocaleDescriptor().adapt;

        Object.assign(this.adaptTranslateService.languages[adaptLocale], {
          'adapt.rx.error.min': this.translateService.instant('com.bmc.arsys.rx.client.error.min'),
          'adapt.rx.error.max': this.translateService.instant('com.bmc.arsys.rx.client.error.max'),
          'adapt.rx.error.required': this.translateService.instant(
            'com.bmc.arsys.rx.client.view-components.validation.required.message'
          ),
          'adapt.select.emptyStateDescription': ''
        });
      })
    );
  }

  private setLocale(locale: string): void {
    this.currentLocale = locale;
    this.setDocumentLang(this.currentLocale);

    const descriptor = this.getCurrentLocaleDescriptor();

    this.adaptTranslateService.useLanguage(descriptor.adapt);
    moment.locale(descriptor.moment);
    this.angularLocale = descriptor.angular;
  }

  private getCurrentLocaleDescriptor(): ILocaleInfo {
    const locale = findKey(SUPPORTED_LOCALES, (localeInfo: ILocaleInfo, key: string) => {
      const language = this.currentLocale.split('-')[0];

      return (
        key === this.currentLocale ||
        key === language ||
        some(localeInfo.similar, (item) => item === this.currentLocale || item === language)
      );
    });

    return {
      angular: locale ? SUPPORTED_LOCALES[locale].angular || locale : DEFAULT_LOCALE,
      moment: (SUPPORTED_LOCALES[locale] && SUPPORTED_LOCALES[locale].moment) || this.currentLocale,
      adapt: (SUPPORTED_LOCALES[locale] && SUPPORTED_LOCALES[locale].adapt) || SUPPORTED_LOCALES[DEFAULT_LOCALE].adapt
    };
  }

  private getLocalizedStringsForLoginPage(): Observable<HttpResponse<ILocalizedStringsForLoginPageResponse>> {
    // When debugging on an RSSO environment, this (/api/rx/application/logincontent/login.json) call fails
    // repeatedly with a http 401 error,so we have bypassed it. This call is only useful for the Innovation
    // Studio login page which is not accessed in RSSO environment.
    return this.rxRssoDebugService.isRssoDebugEnabled()
      ? of()
      : this.httpClient.get<ILocalizedStringsForLoginPageResponse>('/api/rx/application/logincontent/login.json', {
          observe: 'response'
        });
  }

  private setDocumentLang(lang: string): void {
    this.document.documentElement.setAttribute('lang', lang);
  }

  // Safari returns the web browser locale information Region in lowercase
  // en-us instead of en-US which causes issues with localized field.
  private normalizeLocale(locale: string): string {
    const localeInformation = locale.split('-');

    if (localeInformation.length > 1) {
      localeInformation[1] = localeInformation[1].toUpperCase();
    }

    return localeInformation.join('-');
  }
}
