import { IDataPageParams } from './data-page-params.interface';
import { IDataPageRequestConfiguration } from './data-page-request-configuration.interface';
import { Injector } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { IDataPageResult } from './data-page-result.interface';
import { RX_DATA_PAGE } from './data-page.constant';

const DATA_PAGE_API_URL = '/api/rx/application/datapage';

export class DataPage {
  http: HttpClient;
  dataPageType: string;
  configuration: IDataPageRequestConfiguration;

  private requiredRequestParams = {
    pageSize: -1,
    startIndex: 0
  };

  constructor(
    injector: Injector,
    dataPageType: string,
    defaultDataPageRequestConfiguration: IDataPageRequestConfiguration = {}
  ) {
    this.http = injector.get(HttpClient);
    this.dataPageType = dataPageType;
    this.configuration = defaultDataPageRequestConfiguration;
  }

  get(dataPageRequestConfiguration: IDataPageRequestConfiguration = {}): Observable<IDataPageResult> {
    const requestParams: IDataPageParams = {
      ...this.requiredRequestParams,
      ...this.configuration.params,
      ...dataPageRequestConfiguration.params
    };

    const requestHeaders = {
      ...this.configuration.headers,
      ...dataPageRequestConfiguration.headers
    };

    let params = new HttpParams().set('dataPageType', this.dataPageType);

    Object.keys(requestParams).forEach((name) => {
      params = params.set(name, String(requestParams[name]));
    });

    return this.http.get<IDataPageResult>(DATA_PAGE_API_URL, {
      headers: new HttpHeaders(requestHeaders),
      params
    });
  }

  getEmptyDataPage(): Observable<IDataPageResult> {
    return of(RX_DATA_PAGE.emptyDataPage);
  }

  post(dataPageRequestConfiguration: IDataPageRequestConfiguration = {}): Observable<IDataPageResult> {
    const requestParams: IDataPageParams = {
      ...this.requiredRequestParams,
      ...this.configuration.params,
      ...dataPageRequestConfiguration.params
    };

    const requestHeaders = {
      ...this.configuration.headers,
      ...dataPageRequestConfiguration.headers
    };

    const requestBody = { values: { dataPageType: this.dataPageType } };

    Object.keys(requestParams).forEach((name) => {
      requestBody.values[name] = String(requestParams[name]);
    });

    return this.http.post<IDataPageResult>(DATA_PAGE_API_URL, requestBody, {
      headers: new HttpHeaders(requestHeaders)
    });
  }
}
