import { Injectable } from '@angular/core';
import { compact, flow, filter, find, flatten, map as _map, sortBy, uniq } from 'lodash';
import { forkJoin, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { IDesignerStencilElement, IDesignerStencilElementGroups, IDesignerStencilElementTree } from './designer.types';

@Injectable({
  providedIn: 'root'
})
export class RxDesignerStencilBuilder {
  buildElementsTree(
    elementsSources: Observable<IDesignerStencilElement[]>[],
    elementGroups: IDesignerStencilElementGroups
  ): Observable<IDesignerStencilElementTree[]> {
    return forkJoin(elementsSources).pipe(
      map((elements) => {
        return flow(
          flatten,
          (chainElements) => _map(chainElements, 'group'),
          uniq,
          compact,
          (groupNames: string[]) => sortBy(groupNames, (groupName: string) => groupName.toLowerCase()),
          (groupNames: string[]) =>
            sortBy(
              groupNames,
              (groupName: string) => (find(elementGroups, { name: groupName }) || elementGroups.default).priority
            ),
          (groupNames: string[]) =>
            _map(groupNames, (groupName: string) => {
              return {
                label: groupName,
                children: flow(
                  flatten,
                  (chainElements: IDesignerStencilElement[]) => filter(chainElements, { group: groupName }),
                  (chainElements: IDesignerStencilElement[]) =>
                    sortBy(chainElements, (element) => element.label.toLowerCase())
                )(elements)
              };
            })
        )(elements);
      })
    );
  }
}
