import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { shareReplay } from 'rxjs/operators';
import { IUser } from './user.interface';

@Injectable({
  providedIn: 'root'
})
export class RxCurrentUserService {
  private userSubject$ = new BehaviorSubject<IUser | null>(null);
  user$: Observable<IUser | null> = this.userSubject$.asObservable().pipe(shareReplay(1));

  getPreferredLocale(): string {
    return this.get()?.preferredLocale ?? '';
  }

  getPreferredUserLocale(): string {
    return this.get()?.preferredUserLocale ?? '';
  }

  getName(): string {
    return this.get()?.loginName ?? '';
  }

  set(user: IUser): void {
    this.userSubject$.next(user);
  }

  get(): IUser | null {
    return this.userSubject$.getValue();
  }

  isBusinessAnalyst(): boolean {
    return this.get()?.isBusinessAnalyst ?? false;
  }

  isAdministrator(): boolean {
    return this.get()?.isAdministrator ?? false;
  }

  getEditableBundles(): string[] {
    return this.get()?.editableBundles || [];
  }

  isSupportStaff(): boolean {
    return this.get()?.supportStaff ?? false;
  }

  isAvailableForAssignment(): boolean {
    return this.get()?.assignmentAvailable ?? false;
  }

  setAssignmentAvailability(assignmentAvailable: boolean): void {
    const user = this.get();

    if (user) {
      user.assignmentAvailable = assignmentAvailable;
    }
  }
}
