import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { RxLocalizationService } from '../localization';
import { RX_SESSION } from '../security/session.constant';

@Injectable({
  providedIn: 'root'
})
export class RxLoginPageGuard implements CanActivate {
  constructor(private rxLocalizationService: RxLocalizationService, private router: Router) {}

  canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<UrlTree | boolean> {
    return this.rxLocalizationService.loginLocalizedStrings$.pipe(
      map((response) => {
        const ssoProviderType = response.headers.get('sso-provider-type');

        if (ssoProviderType === RX_SESSION.ssoProviderTypes.rsso) {
          const bundleId = next.paramMap.get('bundleId');

          return this.router.parseUrl(bundleId);
        } else {
          return true;
        }
      })
    );
  }
}
