import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map, tap } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { RX_USER_PREFERENCES } from './user-preferences.constants';
import { RxJsonParserService } from '@helix/platform/utils';
import { IUserPreferencesSetting } from './interfaces/user-preferences-setting.interface';
import { IUserPreferencesData } from './interfaces/user-preferences-data.interface';
import { map as _map, get, isEmpty } from 'lodash';
import { IPlainObject } from '../common-types/plain-object.interface';

@Injectable({
  providedIn: 'root'
})
export class RxUserPreferencesService {
  settingsByComponentId: Map<string, IUserPreferencesSetting[]> = new Map();
  apiUrl = '/api/rx/application/admin-settings/user-preference/UI Preferences';

  constructor(private httpClient: HttpClient, private rxJsonParserService: RxJsonParserService) {}

  getUiComponentPreferences<T = IPlainObject>(guid: string): Observable<T> {
    return this.httpClient
      .get(this.apiUrl, {
        headers: new HttpHeaders({
          'default-bundle-scope': ''
        }),
        params: {
          componentId: guid
        }
      })
      .pipe(
        map(({ values }: any) => {
          this.settingsByComponentId.set(guid, values);

          const preferenceSetting: IUserPreferencesSetting = values.find(
            (value) => value.settingName === RX_USER_PREFERENCES.fieldPreferences
          );

          const settingValue = preferenceSetting ? preferenceSetting.settingValue : null;

          return this.rxJsonParserService.tryParseJson(settingValue);
        })
      );
  }

  prepareUiPreferences(data: IUserPreferencesData, guid: string): IUserPreferencesSetting[] {
    if (!isEmpty(this.settingsByComponentId.get(guid))) {
      return this.createUpdatedSettings(data, guid);
    }

    const newSettings = _map(data, (value, name) => ({
      componentName: RX_USER_PREFERENCES.componentName,
      settingName: name,
      settingValue: value,
      assigneeGroupPermission: ''
    }));

    return newSettings;
  }

  setUiComponentPreferences(data: IUserPreferencesData, guid: string): Observable<any> {
    const settings = this.prepareUiPreferences(data, guid);
    const currentSettings = this.settingsByComponentId.get(guid);
    const id = get(currentSettings, '[0].ownerKeyValue1');

    const httpOptions = {
      headers: new HttpHeaders({
        'default-bundle-scope': ''
      })
    };

    return id
      ? this.httpClient.put(`${this.apiUrl}/${id}`, settings, httpOptions)
      : this.httpClient
          .post(this.apiUrl, settings, httpOptions)
          .pipe(tap(() => this.getUiComponentPreferences(data.componentId).subscribe()));
  }

  private createUpdatedSettings(data: IUserPreferencesData, guid: string): IUserPreferencesSetting[] {
    const newSettings = this.settingsByComponentId.get(guid);
    const preferenceSetting = newSettings.find(
      (element) => element.settingName === RX_USER_PREFERENCES.fieldPreferences
    );
    const index = newSettings.indexOf(preferenceSetting);

    preferenceSetting.settingValue = data.preferences;
    newSettings[index] = preferenceSetting;

    return newSettings;
  }
}
