import { Injectable } from '@angular/core';
import { concat, Observable, of } from 'rxjs';
import { mapTo, shareReplay, tap } from 'rxjs/operators';
import { RxSystemConfigurationService } from '../administration';
import { RxOverlayService } from '../overlay/overlay.service';
import { IUser, RxCurrentUserService, RxUserService } from '../user';

@Injectable({
  providedIn: 'root'
})
export class RxSessionService {
  private ssoProviderType: string = null;

  sessionActive$ = concat(this.initCurrentUser(), this.rxSystemConfigurationService.initialize()).pipe(
    mapTo(true),
    shareReplay(1)
  );

  constructor(
    private rxCurrentUserService: RxCurrentUserService,
    private rxUserService: RxUserService,
    private rxOverlayService: RxOverlayService,
    private rxSystemConfigurationService: RxSystemConfigurationService
  ) {}

  initSession(): Observable<boolean> {
    return this.isAlive() ? of(true) : this.sessionActive$;
  }

  isAlive(): boolean {
    return Boolean(this.rxCurrentUserService.get());
  }

  getSsoProviderType(): string {
    return this.ssoProviderType;
  }

  private initCurrentUser(): Observable<IUser> {
    return this.rxUserService.getCurrentUser().pipe(
      tap((user) => {
        this.rxCurrentUserService.set(user);
        this.rxOverlayService.setCurrentOverlayContextOnSessionInit(user);
        this.ssoProviderType = user.ssoProviderType;
      })
    );
  }
}
