import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { truncate } from 'lodash';
import { RxLogService } from '../logging/log.service';
import { RxNotificationService } from '../notification/notification.service';
import { RX_ERROR_HANDLING } from './error-handling.constant';

@Injectable({
  providedIn: 'root'
})
export class RxScriptErrorHandler {
  constructor(
    private rxNotificationService: RxNotificationService,
    private rxLogService: RxLogService,
    private translateService: TranslateService
  ) {}

  handle(error: Error): void {
    const rxErrorMessage = truncate(
      `${this.translateService.instant('com.bmc.arsys.rx.client.error-handling.script-error.message')} ${
        error.message
      }`,
      {
        length: RX_ERROR_HANDLING.maxErrorMessageLength
      }
    );

    this.rxNotificationService.addErrorMessage(
      rxErrorMessage,
      this.translateService.instant('com.bmc.arsys.rx.client.error-handling.script-error.title'),
      {
        suppressLog: true
      }
    );

    this.rxLogService.error(error.stack);
  }
}
