import { Injectable } from '@angular/core';
import { take } from 'rxjs/operators';
import { RxGlobalCacheService } from '../caching/global-cache.service';
import { IRxApplicationInitializer } from './application-initializer.interface';
import { IBundleDescriptor } from '../bundle/bundle.interfaces';

@Injectable({
  providedIn: 'root'
})
export class RxApplicationRegistryService {
  constructor(private rxGlobalCacheService: RxGlobalCacheService) {}

  register(applicationId: string, initializer: IRxApplicationInitializer): void {
    this.rxGlobalCacheService
      .getApplicationBundleDescriptor()
      .pipe(take(1))
      .subscribe((bundleDescriptor: IBundleDescriptor) => {
        if (bundleDescriptor?.id === applicationId) {
          initializer.initialize();
        }
      });
  }
}
