import { DOCUMENT } from '@angular/common';
import { Inject, Injectable } from '@angular/core';
import { CanActivate } from '@angular/router';
import { Observable } from 'rxjs';
import { mapTo, shareReplay, switchMapTo } from 'rxjs/operators';
import { RxSessionService } from '../security/session.service';
import { RxThemingService } from '../theming';

@Injectable({
  providedIn: 'root'
})
export class RxThemeResolver implements CanActivate {
  private themeLoaded$ = new Observable<void>((observer) => {
    const link = this.document.createElement('link');

    link.rel = 'stylesheet';
    link.href = '/api/rx/application/theme/adapt-css-bs4.css';

    link.onload = () => {
      this.rxThemingService.setCssVariables();
      observer.next();
      observer.complete();
    };

    link.onerror = () => {
      observer.next();
      observer.complete();
    };

    this.document.head.appendChild(link);
  }).pipe(shareReplay(1));

  constructor(
    @Inject(DOCUMENT) private document: any,
    private rxSessionService: RxSessionService,
    private rxThemingService: RxThemingService
  ) {}

  canActivate(): Observable<boolean> {
    return this.rxSessionService.sessionActive$.pipe(switchMapTo(this.themeLoaded$), mapTo(true));
  }
}
