import { Injectable } from '@angular/core';
import { RxStringService } from '@helix/platform/utils';
import { includes } from 'lodash';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { RX_ADMINISTRATION, RxAdminSettingsService } from '../administration';
import { RX_APPLICATION } from './application.constant';

@Injectable({
  providedIn: 'root'
})
export class RxAngularApplicationService {
  private angularJsApplicationBundleIds$: Observable<string[]> = this.rxAdminSettingsService
    .getComponentGridData(RX_ADMINISTRATION.settingNames.newApplicationUiOptOut, {
      'default-bundle-scope': RX_APPLICATION.environmentConfigurationBundleId
    })
    .pipe(
      map((gridComponentSettings) =>
        gridComponentSettings.rows
          .filter((newApplicationUiOptOutSetting) => newApplicationUiOptOutSetting['Use old application UI'] === 'true')
          .map((newApplicationUiOptOutSetting) => newApplicationUiOptOutSetting['Application ID'] as string)
          .concat(RX_APPLICATION.angularJsApplicationBundleIds)
      ),
      shareReplay(1)
    );

  private angularJsViewDesignerBundleIds$: Observable<string[]> = this.rxAdminSettingsService
    .getComponentGridData(RX_ADMINISTRATION.settingNames.newViewDesignerOptOut, {
      'default-bundle-scope': RX_APPLICATION.environmentConfigurationBundleId
    })
    .pipe(
      map((gridComponentSettings) =>
        gridComponentSettings.rows
          .filter((newViewDesignerOptOutSetting) => newViewDesignerOptOutSetting['Use old view designer'] === 'true')
          .map((newViewDesignerOptOutSetting) => newViewDesignerOptOutSetting['Application ID'])
          .concat(RX_APPLICATION.angularJsViewDesignerBundleIds)
      ),
      shareReplay(1)
    );

  constructor(private rxAdminSettingsService: RxAdminSettingsService, private rxStringService: RxStringService) {}

  isAngularJsApplication(bundleId): Observable<boolean> {
    return this.angularJsApplicationBundleIds$.pipe(
      map((angularJsApplicationBundleIds) => includes(angularJsApplicationBundleIds, bundleId))
    );
  }

  isAngularJsViewDesignerBundle(bundleId): Observable<boolean> {
    return this.angularJsViewDesignerBundleIds$.pipe(
      map((angularJsViewDesignerBundleIds) => this.rxStringService.isIncluded(bundleId, angularJsViewDesignerBundleIds))
    );
  }
}
