import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable, of } from 'rxjs';
import { catchError, map, switchMap } from 'rxjs/operators';
import { RxGlobalCacheService } from '../caching/global-cache.service';
import { RxSessionService } from '../security/session.service';

@Injectable({
  providedIn: 'root'
})
export class RxValidApplicationGuard implements CanActivate {
  private unknownApplicationUrlTree = this.router.parseUrl('/unknown-application');

  constructor(
    private rxGlobalCacheService: RxGlobalCacheService,
    private router: Router,
    private rxSessionService: RxSessionService
  ) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<UrlTree | boolean> {
    return this.rxSessionService.sessionActive$.pipe(switchMap(() => this.checkBundleState(route)));
  }

  private checkBundleState(route: ActivatedRouteSnapshot): Observable<UrlTree | boolean> {
    const bundleId = route.paramMap.get('bundleId');

    if (bundleId) {
      return this.rxGlobalCacheService.getBundleDescriptor(bundleId).pipe(
        map((bundleDescriptor) => bundleDescriptor.isApplication || this.unknownApplicationUrlTree),
        catchError(() => of(this.unknownApplicationUrlTree))
      );
    } else {
      return of(this.unknownApplicationUrlTree);
    }
  }
}
