import { IComponentCanDeactivate } from './component-can-deactivate.interface';
import { Injectable } from '@angular/core';
import { CanDeactivate, NavigationEnd, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { filter } from 'rxjs/operators';
import { RxModalService, RxUtilityModalsService } from '@helix/platform/ui-kit';

@Injectable({
  providedIn: 'root'
})
export class RxComponentCanDeactivateGuard implements CanDeactivate<IComponentCanDeactivate> {
  pageComponent: IComponentCanDeactivate | undefined;
  private isEnabled = true;

  constructor(
    private router: Router,
    private rxModalService: RxModalService,
    private rxUtilityModalsService: RxUtilityModalsService
  ) {
    window.addEventListener('beforeunload', (event) => {
      if (
        this.isEnabled &&
        (this.pageComponent?.canDeactivate() === false ||
          this.rxModalService.isAnyDockedPanelDirty() ||
          this.rxModalService.isAnyModalDirty())
      ) {
        event.returnValue = true;
      }
    });

    this.router.events.pipe(filter((event) => event instanceof NavigationEnd)).subscribe(() => this.enable());
  }

  canDeactivate(component?: IComponentCanDeactivate): Observable<boolean> | Promise<boolean> | boolean {
    if (this.isEnabled && (this.rxModalService.isAnyDockedPanelDirty() || this.rxModalService.isAnyModalDirty())) {
      return this.rxUtilityModalsService.confirmUnsavedChanges();
    } else if (this.isEnabled && this.pageComponent?.canDeactivate() === false) {
      return this.pageComponent.confirmDeactivation();
    } else {
      return true;
    }
  }

  setPageComponent(component: IComponentCanDeactivate) {
    this.pageComponent = component;
  }

  enable(): void {
    this.isEnabled = true;
  }

  disable(): void {
    this.isEnabled = false;
  }
}
