import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { AdaptMessageService } from '@bmc-ux/adapt-angular';
import { TranslateService } from '@ngx-translate/core';
import { Message } from 'primeng/api';
import { RxNotificationService } from '../notification.service';
import { RxSessionService } from '../../security/session.service';

@Component({
  selector: 'rx-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.scss']
})
export class RxNotificationComponent implements OnInit {
  constructor(
    private rxNotificationService: RxNotificationService,
    private rxSessionService: RxSessionService,
    private translateService: TranslateService,
    private changeDetector: ChangeDetectorRef,
    private adaptMessageService: AdaptMessageService
  ) {}

  ngOnInit(): void {
    this.rxNotificationService.messages$.subscribe((notificationMessage: Message) => {
      const issueData = { ...notificationMessage.data, severity: notificationMessage.severity };

      const link =
        issueData.enableIssueReporting && this.rxSessionService.isAlive()
          ? this.translateService.instant('com.bmc.arsys.rx.client.issue-reporter.report-issue.label')
          : null;

      this.adaptMessageService.add({
        ...notificationMessage,
        link,
        linkHandler: () => {
          this.rxNotificationService.reportIssue(issueData);
        }
      });

      this.changeDetector.detectChanges();
    });
  }
}
