import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { cloneDeep, flatten, forEach, isRegExp, isUndefined, sortBy } from 'lodash';
import { IDataDictionary } from '../data-dictionary/data-dictionary.types';
import {
  IExpressionConfigurator,
  IExpressionConfiguratorDefaultPropertyConfig,
  IExpressionConfiguratorPropertyConfig
} from './expression-configurator.interfaces';
import {
  ExpressionOperatorGroup,
  ExpressionOperatorRowsByGroup,
  IExpressionOperator,
  IExpressionOperatorRow
} from './expression-configurator-operator.types';

export abstract class RxExpressionConfigurator implements IExpressionConfigurator {
  abstract commonDataDictionary$: Observable<IDataDictionary>;

  private expressionConfigs: IExpressionConfiguratorPropertyConfig[] = [];

  protected getDefaultConfig(): IExpressionConfiguratorDefaultPropertyConfig {
    return {
      dataDictionary$: this.commonDataDictionary$,
      operators: ExpressionOperatorRowsByGroup.get(ExpressionOperatorGroup.Math),
      validateExpression: (propertyPath, expression) => of(true)
    };
  }

  configureForProperty(config: IExpressionConfiguratorPropertyConfig): void {
    this.expressionConfigs.push({
      ...this.getDefaultConfig(),
      ...config
    });
  }

  getDataDictionary(propertyPath?: string): Observable<IDataDictionary> {
    return this.getExpressionConfig(propertyPath).dataDictionary$.pipe(
      map((dataDictionary) => this.sortDataDictionary(dataDictionary))
    );
  }

  getOperators(propertyPath?: string): IExpressionOperator[] {
    return flatten(this.getOperatorRows(propertyPath));
  }

  getOperatorRows(propertyPath?: string): IExpressionOperatorRow[] {
    return this.getExpressionConfig(propertyPath).operators;
  }

  getOperatorRowsByGroup(group: ExpressionOperatorGroup): IExpressionOperatorRow[] {
    return cloneDeep(ExpressionOperatorRowsByGroup.get(group));
  }

  protected getExpressionConfig(propertyPath: string): IExpressionConfiguratorPropertyConfig {
    return (
      this.expressionConfigs.find((config) =>
        isRegExp(config.propertyPath) ? config.propertyPath.test(propertyPath) : config.propertyPath === propertyPath
      ) || {
        propertyPath,
        ...this.getDefaultConfig()
      }
    );
  }

  validateProperty(propertyPath: string, propertyValue: string): Observable<boolean> {
    return propertyValue
      ? this.getExpressionConfig(propertyPath).validateExpression(propertyPath, propertyValue)
      : of(true);
  }

  private sortDataDictionary(dataDictionary: IDataDictionary): IDataDictionary {
    forEach(dataDictionary, (node) => {
      if (node.children) {
        node.children = this.sortDataDictionary(
          sortBy(node.children, [(child) => !isUndefined(child.expression), 'label'])
        );
      }
    });

    return dataDictionary;
  }
}
