import { Injectable, Injector } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';

import { IValidationIssue, ValidationIssueType } from '@helix/platform/ui-kit';
import { RxStringService } from '@helix/platform/utils';

import { IPlainObject } from '../common-types/plain-object.interface';
import { RxDesignerCacheService } from '../caching/designer-cache.service';
import { IActionType } from '../action-type/action-type.interfaces';
import { IServerActionModel } from '../designer/designer.types';

@Injectable({
  providedIn: 'root'
})
export abstract class RxServerActionService {
  private rxDesignerCacheService: RxDesignerCacheService = this.injector.get(RxDesignerCacheService);
  protected rxStringService: RxStringService = this.injector.get(RxStringService);
  protected translateService: TranslateService = this.injector.get(TranslateService);

  protected constructor(protected injector: Injector) {}

  protected abstract getModelFromDefinition(definition: any): IServerActionModel;

  protected getActionTypeByName(actionTypeName: string): IActionType {
    return this.rxDesignerCacheService.getActionTypeByNameSync(actionTypeName);
  }

  // TODO-VS: update types
  protected getClassConfig(options: IPlainObject): IPlainObject {
    const model = this.getModelFromDefinition({
      actionTypeName: options.actionTypeName
    } as any);

    return {
      content: model.name,
      elementModel: model,
      icon: model.isDeprecated ? 'info' : null,
      position: options.position,
      type: model.type
    };
  }

  // TODO-VS: update availableCells type
  protected validateInputMap(model: IServerActionModel, availableCells: any[]): Observable<IValidationIssue[]> {
    return of([]);
  }

  // TODO-VS: update availableCells type
  protected validateServerAction(model: IServerActionModel, availableCells: any[]): Observable<IValidationIssue[]> {
    return this.validateInputMap(model, availableCells).pipe(
      map((inputMapValidationIssues) => {
        const validationIssues: IValidationIssue[] = [...inputMapValidationIssues];

        if (this.rxStringService.isEmptySafe(model.name)) {
          validationIssues.push({
            type: ValidationIssueType.Error,
            description: this.translateService.instant(
              'com.bmc.arsys.rx.client.designer.validation.cannot-be-blank.message',
              {
                propertyName: this.translateService.instant('com.bmc.arsys.rx.client.common.label.label')
              }
            ),
            data: {
              guid: model.guid,
              inspectorTabIndex: 1
            }
          });
        }

        model.outputMap.forEach((output, index) => {
          if (this.rxStringService.isEmptySafe(output.assignTarget)) {
            validationIssues.push({
              type: ValidationIssueType.Error,
              description: this.translateService.instant(
                'com.bmc.arsys.rx.client.designer.validation.output-map.message',
                {
                  errorMessage: this.translateService.instant(
                    'com.bmc.arsys.rx.client.designer.validation.cannot-be-blank.message',
                    {
                      propertyName: this.translateService.instant(
                        'com.bmc.arsys.rx.client.designer.assignment-expression.target.label'
                      )
                    }
                  )
                }
              ),
              data: {
                fieldName: 'assignTarget',
                guid: model.guid,
                index,
                inspectorTabIndex: 1,
                propertyName: 'outputMap'
              }
            });
          }

          if (this.rxStringService.isEmptySafe(output.expression)) {
            validationIssues.push({
              type: ValidationIssueType.Error,
              description: this.translateService.instant(
                'com.bmc.arsys.rx.client.designer.validation.output-map.message',
                {
                  errorMessage: this.translateService.instant(
                    'com.bmc.arsys.rx.client.designer.validation.cannot-be-blank.message',
                    {
                      propertyName: this.translateService.instant(
                        'com.bmc.arsys.rx.client.designer.assignment-expression.source.label'
                      )
                    }
                  )
                }
              ),
              data: {
                fieldName: 'expression',
                guid: model.guid,
                index,
                inspectorTabIndex: 1,
                propertyName: 'outputMap'
              }
            });
          }
        });

        return validationIssues;
      })
    );
  }
}
